import {
    ADD_CARTS,
    DEL_CARTS,
    ADDCART_VALUE,
    REDUCECART_VAVLUE,
    SELECT_CARTS_LIST,
    ADD_COLLECTION,
    DEL_COLLECTION
} from '../mutations-type'

import router from '../../router';
import { Toast,MessageBox } from 'mint-ui'

const state = {
    carts: localStorage.getItem('carts') ? JSON.parse(localStorage.getItem('carts')) : [], //
    collections: localStorage.getItem('collections') ? JSON.parse(localStorage.getItem('collections')) : [], //
}

const mutations = {
    // 
    [ADD_CARTS] (state,data) {
        var cartsId = state.carts.find(list => {
            return data.id == list.id
        });
        if(!localStorage.getItem('user')) {
            MessageBox({
                title: '',
                message: '',
                showCancelButton: true
                }).then(res => {
                if(res == 'confirm') {
                    router.push('/login')
                }
            });
            return false;
        }
        if (cartsId) {
            Toast({
                message:'',
                duration: 1500
            });
            } else {
            Toast({
                message:'',
                duration: 1500
            });
            data['select'] = false
            state.carts.push(data);
            localStorage.setItem('carts',JSON.stringify(state.carts));
        }
    },
    // 
    [DEL_CARTS] (state,index) {
        MessageBox({
            title: '',
            message: ''+ state.carts[index].name +'?',
            showCancelButton: true
            }).then(res => {
                if(res == 'confirm') {
                state.carts.splice(index,1);
                localStorage.setItem('carts',JSON.stringify(state.carts))
                Toast({
                    message: '',
                    duration: 1500
                });
            }
        });
    },
    // 
    [ADDCART_VALUE] (state,index) {
        state.carts[index].value ++ 
        localStorage.setItem('carts',JSON.stringify(state.carts))
    },
    // 
    [REDUCECART_VAVLUE] (state,index) {
        if(state.carts[index].value == 1) {
            MessageBox({
                title: '',
                message: ''+ state.carts[index].name +'?',
                showCancelButton: true
                }).then(res => {
                    if(res == 'confirm') {
                    state.carts.splice(index,1);
                    localStorage.setItem('carts',JSON.stringify(state.carts))
                    Toast({
                        message: '',
                        duration: 1500
                    });
                }
            });
        } else {
            state.carts[index].value --
            localStorage.setItem('carts',JSON.stringify(state.carts))
        }
    },
    // 
    [SELECT_CARTS_LIST] (state,index) {
        state.carts[index].select =! state.carts[index].select
    },
    //
    [ADD_COLLECTION] (state,data) {
        var collectionsId = state.collections.find(list => {
            return data.id == list.id
        });
        if(collectionsId) {
            Toast('')
            return false
        }
        state.collections.push(data)
        localStorage.setItem('collections',JSON.stringify(state.collections));
        Toast('')
    },
    // 
    [DEL_COLLECTION] (state,index) {
        MessageBox({
            title: '',
            message: ''+ state.collections[index].name +'?',
            showCancelButton: true
            }).then(res => {
                if(res == 'confirm') {
                state.collections.splice(index,1);
                localStorage.setItem('collections',JSON.stringify(state.collections))
                Toast({
                    message: '',
                    duration: 1500
                });
            }
        });
    },
}

export default {
    namespaced: true, //
    state,
    mutations
}


